import BaseReader from './BaseReader.vue';
import { RichText } from './../extensions/index.js';
import markdownit from './../markdownit/index.js';
export default {
  name: 'RichTextReader',
  components: {
    BaseReader
  },
  provide: {
    renderHtml(content) {
      return markdownit.render(content);
    },
    extensions() {
      return [RichText.configure({
        editing: false
      })];
    }
  },
  props: {
    content: {
      type: String,
      required: true
    }
  }
};