import { NcDialog } from '@nextcloud/vue';
import { isMobilePlatform } from '../helpers/platform.js';
export default {
  name: 'HelpModal',
  components: {
    NcDialog
  },
  data() {
    return {
      formatted: {
        bold: true,
        italic: true,
        strikethrough: true,
        heading1: true,
        heading6: true,
        unorderdList: true,
        orderedList: true,
        checkList: true,
        blockQuote: true,
        codeBlock: true
      }
    };
  },
  computed: {
    isFormatted() {
      return style => this.formatted[style];
    },
    // Cache the output of `isMobilePlatform()`
    isMobileCached() {
      return this.isMobilePlatform();
    }
  },
  methods: {
    toggleFormatted(style) {
      this.formatted[style] = !this.formatted[style];
    },
    isMobilePlatform
  }
};