import { ERROR_TYPE, IDLE_TIMEOUT } from './../../services/SyncService.js';
import Lock from 'vue-material-design-icons/Lock.vue';
import { NcNoteCard } from '@nextcloud/vue';
import CollisionResolveDialog from '../CollisionResolveDialog.vue';
export default {
  name: 'DocumentStatus',
  components: {
    CollisionResolveDialog,
    Lock,
    NcNoteCard
  },
  props: {
    idle: {
      type: Boolean,
      require: true
    },
    lock: {
      type: Object,
      default: null
    },
    syncError: {
      type: Object,
      default: null
    },
    hasConnectionIssue: {
      type: Boolean,
      require: true
    }
  },
  data() {
    return {
      IDLE_TIMEOUT
    };
  },
  computed: {
    hasSyncCollission() {
      return this.syncError && this.syncError.type === ERROR_TYPE.SAVE_COLLISSION;
    },
    isLoadingError() {
      return this.syncError && this.syncError.type === ERROR_TYPE.LOAD_ERROR;
    },
    hasWarning() {
      return this.syncError || this.hasConnectionIssue;
    }
  },
  methods: {
    reconnect() {
      this.$emit('reconnect');
    },
    reload() {
      window.location.reload();
    }
  }
};