import { useEditorMixin, useIsRichEditorMixin, useSyncServiceMixin } from './Editor.provider.js';
import { NcButton } from '@nextcloud/vue';
import setContent from './../mixins/setContent.js';
export default {
  name: 'CollisionResolveDialog',
  components: {
    NcButton
  },
  mixins: [useEditorMixin, useIsRichEditorMixin, setContent, useSyncServiceMixin],
  props: {
    syncError: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      clicked: false
    };
  },
  methods: {
    resolveThisVersion() {
      this.clicked = true;
      this.$syncService.forceSave().then(() => this.$syncService.syncUp());
      this.$editor.setEditable(!this.readOnly);
    },
    resolveServerVersion() {
      const {
        outsideChange
      } = this.syncError.data;
      this.clicked = true;
      this.$editor.setEditable(!this.readOnly);
      this.setContent(outsideChange, {
        isRich: this.$isRichEditor
      });
      this.$syncService.forceSave().then(() => this.$syncService.syncUp());
    }
  }
};